@import "../../shared-styles";

.home {
  height: 100%;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "intro exploreButton";

  align-items: center;
  justify-items: center;

  .intro {
    grid-area: intro;

    max-width: 450px;
    justify-self: left;

    p {
      text-align: justify;
    }
  }
}

@media only screen and (max-width: 600px) {
  .home {
    height: 100%;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      "intro"
      "exploreButton";

    text-align: center;

    .intro {
      width: 100%;

      p {
        text-align: center;
      }
    }
  }
}
